import React, { useState } from "react";
import {collection} from "@firebase/firestore";
import {useCollectionData} from "react-firebase-hooks/firestore";
import {db} from "./firebase.js";
import ImagesList from "./ImagesList.js";
import "./styles.css";
import { Link } from "react-router-dom";
import AlbumForm from "./AlbumForm.js";
import { deleteDoc, doc } from "firebase/firestore";
import {useEffect} from "react";
import Navbar from "./Navbar.js";

export default function AlbumList(){
    const [formpath,Setpath]=useState(false); 

    const [selectedAlbum,SetselectedAlbum]=useState(null);
    const [heading,Setheading]=useState("");
    const query=collection(db,"Albums");
    const [docs,loading,error]=useCollectionData(query);
    const [formstatus,Setstatus]=useState(false);

    function handleClick(path,n){
        SetselectedAlbum(path);
        Setpath(n);

    }

    async function handleDelete(name){
        await deleteDoc(doc(db, name));
    }

    function handleformstatus(status){
        Setstatus(status);
    }

    return(
        <div className={`App ${loading ? "loading" : ""}`}>
            {loading && <div className="loader-container"><div className="loader"></div></div>}
            <div>
                <Navbar heading={"AlbumList"}
                        path={"Albums"}
                        st={Setpath}
                />
            </div>
            <div className="main-container">
                <div className="left">
                    <h2>{"Yours Albums"}</h2>
                </div>
                <div className="right">
                    {formstatus?"":<button onClick={()=>handleformstatus(true)} className="add-button">New Album</button>}
                </div>
            </div>
            <div>
                {formstatus?<button onClick={()=>handleformstatus(false)} className="cancel-button">X</button>:""}
                {formstatus?<AlbumForm path={"Albums"}/>:""}
            </div>
            {/* <AlbumForm path={"Albums"}/> */}
            <ul className="list">
                {docs?.map((val)=>(
                    <div>
                    <li key={Math.random()} 
                    onClick={()=>{handleClick(`${val.name}`,true)}}>
                        <Link to={`Albums/${val.name}/ImagesList`}>
                            <div className="polaroid">
                                <div className="image-container">
                                    <img src="https://icons.iconarchive.com/icons/toma4025/rumax/256/folder-pictures-icon.png" className="album-size"/>
                                    <div className="button-container">
                                        <button onClick={(e)=>{
                                                                e.preventDefault();
                                                                e.stopPropagation();
                                                                handleDelete(`Albums/${val.name}`)
                                                              }} className="button-edit-delete">Delete</button>
                                    </div>
                                </div>
                                <div className="container">
                                    <p>
                                        {val.name}
                                    </p>
                                </div>
                            </div>
                        </Link>
                    </li>
                    </div>
                ))}
            </ul>
            {selectedAlbum && <ImagesList path={`Albums/${selectedAlbum}/ImagesList`}/>}
            {/* {heading && <Navbar heading={heading}/>} */}
        </div>
    );
}