import React,{useRef} from "react";
import { updateDoc } from "firebase/firestore";
import { collection, addDoc ,setDoc ,doc} from "firebase/firestore"; 
import {db} from "./firebase.js";
import { useParams } from "react-router-dom";
import "./form.css";

export default function Form({path,arr,imagename,formmode}){
    const url=useRef();
    const name=useRef();
    // console.log(id);

    async function handleUpdate(e){
        e.preventDefault();

        const new_selected_image_arr={...arr};

        new_selected_image_arr.name=name.current.value;
        new_selected_image_arr.url=url.current.value;

        const docref = doc(db,`Albums/${imagename}/ImagesList/${arr.id}`);
        await updateDoc(docref,new_selected_image_arr);

        e.target.reset();
    }

    async function handleClick(e){
        e.preventDefault();

        const docref=doc(db,path,name.current.value);
        await setDoc(docref,{
            name:name.current.value,
            url:url.current.value
        });
        
          e.target.reset();
    }

    return(
        // <form onSubmit={handleClick} className="input form">
        // <form onSubmit={arr?handleUpdate:handleClick} className="input form">
        //     <h3>{arr?"Update":"Add"}</h3>
        //     <input placeholder="Name.." ref={name} required/>
        //     <input placeholder="Url.." ref={url} required/>
        //     <button>Add</button>
        // </form>
        <>
        {(arr && imagename && formmode) ?

           (<form onSubmit={handleUpdate} className="input form">
            <h3>Update</h3>
            <input placeholder={arr.name} ref={name} required/>
            <input placeholder={arr.url} ref={url} required/>
            <button>Update</button>
           </form>
           ):(
        <form onSubmit={handleClick} className="input form">
            <h3>Add</h3>
            <input placeholder="Name.." ref={name} required/>
            <input placeholder="Url.." ref={url} required/>
            <button>Add</button>
        </form>
           )
        }
        </>
    );
}