import React, { useState } from "react";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "./styles.css"; // Import your custom CSS for the modal

const ImageCarouselPopup = ({ images, selectedIndex, onClose }) => {
  const [currentIndex, setCurrentIndex] = useState(selectedIndex);

  const handlePrev = () => {
    setCurrentIndex((prevIndex) => (prevIndex === 0 ? images.length - 1 : prevIndex - 1));
  };

  const handleNext = () => {
    setCurrentIndex((prevIndex) => (prevIndex === images.length - 1 ? 0 : prevIndex + 1));
  };

  return (
    <div className="image-carousel-popup-overlay">
      <div className="image-carousel-popup">
        <button className="close-button" onClick={onClose}>
          X
        </button>
        <Carousel showThumbs={false} selectedItem={currentIndex}>
          {images.map((image, index) => (
            <div key={index}>
              <img src={image} alt="Selected" />
            </div>
          ))}
        </Carousel>
        <div className="navigation-buttons">
          {/* <button onClick={handlePrev}>Previous</button>
          <button onClick={handleNext}>Next</button> */}
        </div>
      </div>
    </div>
  );
};

export default ImageCarouselPopup;
