import React,{useRef} from "react";
import {doc,setDoc} from "firebase/firestore"; 
import {db} from "./firebase.js";
import "./styles.css";

export default function AlbumForm({path}){
    const name=useRef();

    async function handleClick(e){
        e.preventDefault();

        const docref=doc(db,path,name.current.value);
        await setDoc(docref,{name:name.current.value});

        e.target.reset();
    }
    return(
        <form onSubmit={handleClick} className="input-album">
            <input placeholder="Name.." ref={name} required/>
            <button>Add</button>
        </form>
    );
}